import React, { useState, useEffect } from 'react';
import { BrowserRouter as Router, Routes, Route } from 'react-router-dom';
import ElevateAppBar from './layout/Navbar';
import Home from './layout/Home';
import Loader from './components/Loader';
import Modal from './components/Model'; 
import './App.css';
import Footer from './layout/Footer';

const App = () => {
  const [loading, setLoading] = useState(true);
  const [isModalOpen, setIsModalOpen] = useState(false); // State for modal visibility

  useEffect(() => {
    const timer = setTimeout(() => {
      setLoading(false);
    }, 2000);

    return () => clearTimeout(timer);
  }, []);

  return (
    <Router>
      <>
          <ElevateAppBar />
          <Routes>
            <Route path="/vizoss" element={<Home />} />
            {/* <Route path="/" element={<Home />} /> */}
          </Routes>
          <div className="floating-message-icon" onClick={() => setIsModalOpen(true)}>
            <i className="fas fa-comment-dots"></i>
          </div>
          <Modal isOpen={isModalOpen} onClose={() => setIsModalOpen(false)} /> {/* Modal component */}
          <Footer />
        </>
    </Router>
  );
};

export default App;
